/* sidebar.css */

.sidebar {
    position: fixed;
    top: 60px;
    left: -300px;
    width: 300px;
    height: 100%;
    background-color: #f0f0f0;
    transition: left 0.3s ease;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* サイドバーに影を追加 */
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .sidebar-content {
    padding: 16px;
  }
  
  button.close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    background-color: #007bff;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
  }
  
  button.toggle-button {
    position: absolute;
    top: 16px;
    left: 20px;
    background-color: #007bff;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
    white-space: nowrap;
  }
  
  .Title-text {
    position: absolute;
    top: 33px;
    left: 130px;
    font-size: 24px;
    transform: translateY(-50%);
    margin-left: 8px;
    writing-mode: horizontal-tb;
    white-space: nowrap; /* テキストが横に改行されないようにする */
  }

  /* サイドバー内のアイテムスタイル */
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    padding: 8px;
    border-bottom: 1px solid #ccc;
  }
  
  li:last-child {
    border-bottom: none;
  }
  